import { useNavigate } from 'react-router-dom';
import { adress, Adress } from './adress';
import { ReactComponent as Clock } from '../../img/icons/clock.svg';
import { ReactComponent as Local } from '../../img/icons/location.svg';
import { ReactComponent as Phone } from '../../img/icons/phone.svg';
import './style/Contacts.scss';

export default function Contacts() {
  return (
    <div className="contacts indentstart indent_last">
      <div className="container">
        <ContactsBlock />
      </div>
    </div>
  );
}

const ContactsBlock: React.FC = () => {
  const buildblocks = () => {
    const arr: JSX.Element[] = [];
    let title = '';

    adress.map((item: Adress) => {
      if (title != item.name) {
        arr.push(<ContactsTitle key={item.name} item={item} />);
      }
      title = item.name;
      arr.push(<ContactsItem key={item.id} item={item} />);
    });

    return arr;
  };

  return <div className="contacts__wrap">{buildblocks()}</div>;
};

function ContactsTitle({ item }) {
  return (
    <div className="contacts__title">
      <div className="contacts__titleitem">
        <h3>{item.name}</h3>
      </div>
    </div>
  );
}

const ContactsItem: React.FC<any> = ({ item }) => {
  const router = useNavigate();
  const route = (name: string) => {
    router(`/contacts/${name}`);
    document.documentElement.scrollIntoView(true);
  };
  return (
    <>
      <div className="contacts__item">
        <div className="contacts__block">
          <div className="contacts__contain">
            <div className="contacts__blockone">
              <div className="contacts__col">
                <div className="contacts__gym">
                  <p className="big">Fight Gym in the {item.subname}</p>
                </div>
                <div className="contacts__work">
                  <div className="contacts__icon">
                    <Clock />
                  </div>
                  <div className="contacts__text">
                    <p className="mini">{item.work}</p>
                  </div>
                </div>
              </div>
            </div>
            <div className="contacts__blocktwo">
              <div className="contacts__blocktwobody">
                <div className="contacts__adress">
                  <div className="contacts__icon">
                    <Local />
                  </div>
                  <div className="contacts__text">
                    <p className="mini">{item.adress}</p>
                  </div>
                </div>
                <div className="contacts__number">
                  <div className="contacts__icon">
                    <Phone />
                  </div>
                  <div className="contacts__text">
                    <p className="mini">{item.number}</p>
                  </div>
                </div>
              </div>
            </div>
            <div className="contacts__blockthree">
              <div onClick={() => route(item.path)} className="contacts__more">
                <p className="mini">See more</p>
              </div>
              <div
                onClick={() => route(item.path)}
                className="contacts__circle"
              >
                <div className="contacts__arrow">{'>'}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
